import { Resizer } from "src/app/components/Resizer";
import { Component } from "src/quickdom/Component";
import { Align } from "src/quickdom/enums/Align";
import { Cursor } from "src/quickdom/enums/Cursor";
import { ImageRepeat } from "src/quickdom/enums/ImageRepeat";
import { ImageSize } from "src/quickdom/enums/ImageSize";
import { Overflow } from "src/quickdom/enums/Overflow";
import type { Bounds } from "src/app/types/Bounds";
import type { LayoutNode } from "src/quickdom/types/LayoutNode";

export class App extends Component {
  private readonly resizer: Resizer;
  public constructor() {
    super();
    this.resizer = Resizer.create();
  }
  public get bounds(): Bounds {
    return this.resizer.bounds;
  }
  public get imageScale(): number {
    return Math.min(1, (this.bounds.width - 80) / 1120);
  }
  public get layout(): LayoutNode {
    return {
      height: this.bounds.height,
      width: this.bounds.width,
      background: "#111",
      image: "images/gui/altBack.png",
      imageSize: ImageSize.Auto,
      imagePosition: Align.MiddleCenter,
      column: [
        {
          height: this.bounds.height,
          width: this.bounds.width,
          background:
            "linear-gradient(90deg, #000a 0%, #0002 25%, #0002 75%, #000a 100%)",
          align: Align.TopCenter,
          overflow: Overflow.Scroll,
          row: [
            {
              grow: 1,
              width: 0,
              maxWidth: 1200,
              margin: { bottom: 100 },
              align: Align.MiddleCenter,
              column: [
                {
                  align: Align.MiddleCenter,
                  width: "100%",
                  column: [
                    {
                      text: "Spirit Island Professional Edition",
                      color: "rgb(255, 230, 192)",
                      shadow: true,
                      size: 24,
                      bold: true,
                    },
                    {
                      text: "as featured in",
                      size: 20,
                      color: "#000",
                    },
                    {
                      row: [
                        {
                          image: "images/gui/youtube.png",
                          width: 30,
                          height: 30,
                          margin: { bottom: 4 },
                          shadow: true,
                        },
                        {
                          width: 10,
                        },
                        {
                          text: "Spirit Island with Jonathan",
                          color: "rgb(217, 220, 255)",
                          size: 20,
                          className: "link",
                          cursor: Cursor.Pointer,
                          shadow: true,
                          onClick: () => {
                            window.location.href =
                              "https://www.youtube.com/channel/UCGkWmm9cDw2edNysDSxrh_A";
                          },
                        },
                      ],
                    },
                  ],
                  height: 80,
                  margin: { top: 60, bottom: 60 },
                },
                {
                  width: "100%",
                  background: "#313031",
                  shadow: true,
                  padding: { left: 10, right: 10, bottom: 10 },
                  column: [
                    {
                      width: "100%",
                      height: 40,
                      align: Align.MiddleLeft,
                      column: [
                        {
                          color: "#ffffe7",
                          text: "A fully featured non-offical Spirit Island client",
                          margin: { left: 5 },
                        },
                      ],
                    },
                    {
                      width: "100%",
                      height: "100%",
                      background: "#414040",
                      shadow: true,
                      padding: 20,
                      column: [
                        this.textBlock(
                          "Spirit Island Professional Edition is a non-official, non-public Spirit Island client made for the primary purpose of making it easier to follow along with Spirit Island games. The principal philosophy is to have a modern, simple, yet powerful, uncluttered, viewer-friendly user interface with the most important information always visible on the screen. This article highlights the most prominent features and design decisions of this client."
                        ),
                        this.image(
                          "images/screens/memoryThematic.png",
                          1140,
                          641
                        ),

                        this.separator,
                        this.header("Comprehensive Rule-Set Implementation"),
                        this.textBlock(
                          "The client incorporates the complete ruleset of all published expansions, encompassing all (37) spirits, aspects, scenarios, adversaries, powers, events, etc."
                        ),
                        this.textBlock(
                          "Every article in the official FAQ (Querki) has been cross-referenced to ensure a close adherence to the official rules."
                        ),
                        this.image("images/screens/sets.png", 1140, 295),

                        this.separator,
                        this.header("Customizable Game Setup"),
                        this.textBlock(
                          "The custom game mode allows setting up any type of Spirit Island game with up to 8 players. This includes an extra board, double adversaries, thematic boards, playing without events, or beginner modes. There are several randomization options to randomize certain or all aspects of the setup."
                        ),
                        this.image("images/screens/setup.png", 1140, 641),

                        this.separator,
                        this.header("Versatile Island Layout"),
                        this.textBlock(
                          "The boards that can be laid out in a recommended, custom, or random configuration."
                        ),
                        this.video("/videos/island.mp4", 1140, 790),

                        this.separator,
                        this.header("Thematic Board Overhaul"),
                        this.textBlock(
                          "The thematic board has undergone several changes compared to the cardboard variant. Firstly, the textures have been altered to make the lands more distinct. Secondly, the edge borders and coast have been adjusted to accommodate any layout configuration, such as the circular board featured below. Finally, some inner borders have been somewhat straightened to compensate for the smaller land sizes, allowing for more token space."
                        ),
                        this.image("images/screens/thematic.png", 1140, 1035),

                        this.separator,
                        this.header("Progressive Campaign System"),
                        this.textBlock(
                          "In addition to custom games, there is also a campaign system where players have to choose between a handful of random encounters and spirits. Every time a player wins, the difficulty increases. If a player loses, the spirit used is retired."
                        ),
                        this.image("images/screens/campaign.png", 1140, 641),

                        this.separator,
                        this.header("Streamlined Gameplay Clarity"),
                        this.textBlock(
                          'Considerable effort has been invested in simplifying the process of understanding our position in the game, determining what needs to be done, and identifying the available options. In the screenshot below, the left panel indicates that we are in the Event phase. In the "Query Box" to the right, it is evident that we are presently resolving the Local Diaspora of this Event Card. The call-to-action prompt is currently prompting us to select a land. On the board, the available lands to choose from are clearly visible.'
                        ),
                        this.image("images/screens/options.png", 1140, 641),

                        this.separator,
                        this.header("Focused Spirit Management"),
                        this.textBlock(
                          "The lower section of the screen is exclusively reserved for the selected Spirit. At any given moment, only one Spirit is displayed, accompanied by a designated area for its power cards. This intentional design choice seeks to concentrate attention on one Spirit at a time, aligning with a common player practice. Instead of transferring cards among different zonessuch as played, discarded, or in handa unified area is employed for all cards. Symbols are utilized to signify each card's status, streamlining clarity and minimizing visual clutter. This approach not only enables the use of full-sized Spirit Boards and Powers but also ensures that all text and graphics remain visible at all times."
                        ),
                        this.image("images/screens/cards.png", 1140, 417),

                        this.separator,
                        this.header("User-Friendly In-Game Card Catalog"),
                        this.textBlock(
                          "The in-game card catalog allows you to look up every card in the game at any time. It features a human-understandable search system. Below, you can find the ten fast major powers with fire elements that cost 2-4 energy."
                        ),
                        this.image("images/screens/catalog.png", 1140, 641),

                        this.separator,
                        this.header("Convenient Reminders & Ongoing Effects"),
                        this.textBlock(
                          "A game of Spirit Island can involve numerous simultaneous effects, making it challenging to remember everything that can influence ones decisions. To address this, reminders, both global and local, have been added for every lingering effect. Yellow reminders signify effects lasting until the end of the turn, while pink reminders indicate permanent effects."
                        ),
                        this.video("/videos/reminders.mp4", 1140, 348),

                        this.separator,
                        this.header("Full History & Undo Features"),
                        this.textBlock(
                          "A comprehensive log of the current game is readily available. The arrows to the right allow players to jump to any specific point in the history. It is up to the players to decide whether jumping back past the point where information was revealed is acceptable or not. Hovering over blue text will highlight that particular land or reveal that specific card."
                        ),
                        this.video("videos/history.mp4", 1140, 1140),

                        this.separator,
                        this.header("Flexible Move Modification"),
                        this.textBlock(
                          'Sometimes you may want to modify a move made several turns ago. While it is possible to jump back to that point, change the move, and then re-enter all the moves again, there is an easier way: with the "Manage Move Divergence" dialog that pops up when one changes a move in the past, you can choose how you want to alter the history. You can simply jump back to the point where you want to change the move, modify that move alone, and then jump back. If doing this renders some other move invalid, the client will notify you, giving you the opportunity to also adjust those moves.'
                        ),
                        this.image(
                          "images/screens/move_divergence.png",
                          507,
                          557
                        ),

                        this.separator,
                        this.header("Admin Mode Access"),
                        this.textBlock(
                          "As an alternative to the above, one can also open up Admin Mode, which allows users to change the current position in any way they want. It can also be used for testing, setting up a specific position, or just experimenting."
                        ),
                        this.image("images/screens/admin_mode.png", 771, 756),

                        this.separator,
                        this.header("Efficient Cross-Platform Performance"),
                        this.textBlock(
                          "Built upon a custom cross-platform graphics engine to ensure optimal performance, the client is highly responsive with a quick start-up time. The decision to render the board in 2D makes it very laptop-friendly, avoiding overheating graphic cards and ensuring compatibility with even older systems. The client supports the main platforms (Windows, Mac, Android, and iOS) with a single code-base and is designed with tablet support in mind."
                        ),

                        this.separator,
                        this.header("Engaging Animation Effects"),
                        this.textBlock(
                          "Even though the main board is rendered in 2D, there are still some simple 3D animations implemented where appropriate."
                        ),
                        this.video("videos/flip.mp4", 1140, 500),

                        this.separator,
                        this.header("Immersive Audio Experience"),
                        this.textBlock(
                          "The client incorporates music that dynamically changes based on the situation, an announcement voice for phases and invader actions, and sound effects that vary from one spirit to another."
                        ),

                        this.separator,
                        this.header("Stable Multiplayer Integration"),
                        this.textBlock(
                          "While the client is not publicly available, it still boasts extensive support for multiplayer. The server infrastructure can handle millions of concurrent games, and the way it is built makes it virtually impossible for players to end up in a state that is out of sync, even when using undo or jumping in history."
                        ),
                        this.extraLine,
                        this.textBlock(
                          "The intended use of multiplayer is in conjunction with an external voice chat application like Discord. Joining a game is as simple as entering the join code found in any game. Whenever somebody performs an action, the game state is automatically updated for all connected clients. The multiplayer principle is that everyone can perform any action, similar to a physical board setting. It is left to the players to decide who does what and in what manner. There are no forced voting systems, etc. However, there are some features to facilitate multiplayer, such as pinging a land of interest to the rest of the group."
                        ),
                        this.image("images/screens/multiplayer.png", 1140, 376),

                        this.separator,
                        this.header("Integrated Game Library"),
                        this.textBlock(
                          "Every move of every game is automatically saved in the library. Here, one can view overall statistics and jump straight into any of the games. Additionally, there is support for importing and exporting games."
                        ),
                        this.image("images/screens/stats.png", 1140, 606),

                        this.separator,
                        this.header("Powerful A.I. Capabilities"),
                        this.textBlock(
                          'Users can also mix and match human players with A.I. players. While the A.I. may not be as strong as the most skilled human players, it is sufficiently robust to win many level 6 adversary matchups, as illustrated in the example below where Fathomless Mud of the Swamp conquers Sweden level 6. It is at the discretion of the players when and if to invoke the A.I. by pressing the A.I. button. Unfortunately, strategy discussions with the A.I. are not possible, so it functions more like the scenario "A Diversity of Spirits". The A.I. can also run without a user interface to simulate thousands of games, either for self-improvement or to determine aspects like which Major is the most effective.'
                        ),
                        this.video("videos/ai.mp4", 1140, 500),
                        this.separator,
                        this.header("Summary"),
                        this.textBlock(
                          "Essentially, the Spirit Island Professional Edition is a custom Spirit Island client with some notable features. These encompass a comprehensive rule-set, a streamlined and uncluttered user interface, an integrated A.I, an enhanced thematic board, effective audio and animations, and cross-platform multiplayer support. With a robust technical foundation the client also provides essential tools such as the traversable game history, searchable card catalog, and a game library. Together, these features contribute to a versatile environment for playing and showcasing Spirit Island games."
                        ),
                      ],
                    },
                  ],
                },
              ],
            },
          ],
        },
      ],
      mayRightClick: false,
    };
  }
  private get extraLine(): LayoutNode {
    return {
      width: "100%",
      height: 10,
    };
  }
  private get header() {
    return (text: string): false | LayoutNode => {
      return {
        color: "rgb(255, 230, 192)",
        size: 20,
        bold: true,
        margin: { top: 10, bottom: 10 },
        text,
      };
    };
  }
  private get image() {
    return (
      image: string,
      width: number,
      height: number
    ): false | LayoutNode => {
      return {
        width: 1140 * this.imageScale,
        height: height * this.imageScale,
        image,
        imageSize: {
          width: width * this.imageScale,
          height: height * this.imageScale,
        },
        imageRepeat: ImageRepeat.NoRepeat,
        imagePosition: Align.MiddleCenter,
        margin: { top: 20, bottom: 20 },
        shadow: true,
      };
    };
  }
  private get separator(): LayoutNode {
    return {
      className: "separator",
      width: "100%",
      margin: { top: 10, bottom: 10 },
    };
  }
  private get textBlock() {
    return (text: string): false | LayoutNode => {
      return {
        className: "text",
        color: "#fff",
        size: 16,
        text,
      };
    };
  }
  private get video() {
    return (
      source: string,
      width: number,
      height: number
    ): false | LayoutNode => {
      return {
        video: {
          autoplay: true,
          loop: true,
          source,
          width: width * this.imageScale,
          height: height * this.imageScale,
        },
        width: width * this.imageScale,
        height: height * this.imageScale,
        margin: { top: 20, bottom: 20 },
        shadow: true,
      };
    };
  }
}
