import { Observable } from "src/quickdom/utilities/Observable";
import type { LayoutNode } from "src/quickdom/types/LayoutNode";

export class Component {
  public get layout(): LayoutNode {
    return {};
  }
  public static create<S extends Array<any>, T extends Component>(
    this: new (...args: S) => T,
    ...args: S
  ): T {
    const component = new this(...args);
    Observable.observableClass(component, this.name);
    component.init();
    return component;
  }
  public static layout<
    P extends { [key: string]: any },
    T extends Component,
    U extends Component
  >(
    this: new (parent: U, props: P, ...args: any[]) => T,
    parent: U,
    options?: {
      id?: string;
    } & P,
    ...args: any[]
  ): LayoutNode {
    return Component.create.call(this as any, parent, options, ...args).layout;
  }
  public init(): void {}
}
