import {
  $mobx,
  makeObservable as mobxMakeObservable,
  isObservable,
  autorun,
  toJS,
} from "mobx";

const annotationsSymbol = Symbol("annotationCache");
const objectPrototype = Object.prototype;

export class Observable {
  public static reactivity = true;
  public static makeObservable<T extends object>(target: T): void {
    if (this.reactivity) {
      mobxMakeObservable(target);
    }
  }
  public static observableClass(obj: any, prefix?: string): void {
    if (this.reactivity) {
      this.makeAutoObservable(obj);
      const descriptor = Object.getOwnPropertyDescriptor(obj, "dataStore");
      if (prefix !== undefined && descriptor?.get) {
        const localKey = `sid-${prefix}`;
        const valueStr = localStorage.getItem(localKey);
        if (valueStr) {
          const value = JSON.parse(valueStr, (_, v) => {
            return v === "undefined" ? undefined : v;
          });
          obj.dataStore = value;
        }
        setTimeout(() => {
          autorun(() => {
            localStorage.setItem(
              localKey,
              JSON.stringify(toJS(obj.dataStore), (_, v) => {
                return v === undefined ? "undefined" : v;
              })
            );
          });
        }, 1);
      }
    }
  }
  private static makeAutoObservable<T extends object>(target: T): void {
    if (this.reactivity) {
      if (isObservable(target)) {
        throw new Error("Target must not be observable");
      }
      let annotations: any = (target as any)[annotationsSymbol];
      if (!annotations) {
        annotations = {};
        const proto = Object.getPrototypeOf(target);
        let current = target;
        while (current && current !== objectPrototype) {
          Reflect.ownKeys(current).forEach((key) => {
            if (key !== $mobx && key !== "constructor" && key !== "toString") {
              annotations[key] = true;
            }
          });
          current = Object.getPrototypeOf(current);
        }
        if (proto && proto !== objectPrototype) {
          Object.defineProperty(proto, annotationsSymbol, {
            value: annotations,
          });
        }
      }
      mobxMakeObservable(target, annotations, { autoBind: true });
    }
  }
}
