import { action, reaction } from "mobx";
import { Align } from "src/quickdom/enums/Align";
import { Parameter } from "src/quickdom/enums/Parameter";
import { implicitPx } from "src/quickdom/utilities/implicitPx";
import { isTouchDevice } from "src/quickdom/utilities/isTouchDevice";
import { objectsAreEqual } from "src/quickdom/utilities/objectsAreEqual";
import type { Component } from "src/quickdom/Component";
import type { InputNode } from "src/quickdom/types/InputNode";
import type { LayoutNode } from "src/quickdom/types/LayoutNode";
import type { LayoutNodeProps } from "src/quickdom/types/LayoutNodeProps";
import type { TextNode } from "src/quickdom/types/TextNode";
import type { VideoNode } from "src/quickdom/types/VideoNode";

interface TrackedHTMLElement extends HTMLElement {
  oldCaptureClickEventListener?: EventListenerOrEventListenerObject;
  oldEventListener?: EventListenerOrEventListenerObject;
}

export abstract class QuickDOM {
  private static readonly INPUT_MAP = {
    value: (element: HTMLInputElement, node: InputNode) => {
      element.value = node.value ?? "";
    },
    onChange: (element: HTMLInputElement, node: InputNode) => {
      element.onchange = this.addListener(node.onChange, node.enabled);
    },
    onInput: (element: HTMLInputElement, node: InputNode) => {
      element.oninput = this.addListener(node.onInput, node.enabled);
    },
    enabled: (element: HTMLInputElement, node: InputNode) => {
      QuickDOM.INPUT_MAP.onChange(element, node);
    },
    autoFocus: () => {
      // Only focuses on first render
    },
    autoSelect: () => {
      // Only selects on first render
    },
    spellCheck: (element: HTMLInputElement, node: InputNode) => {
      element.spellcheck =
        node.spellCheck !== undefined ? node.spellCheck : true;
    },
    ref: (element: HTMLInputElement, node: InputNode) => {
      node.ref?.(element);
    },
    accept: (element: HTMLInputElement, node: InputNode) => {
      element.accept = node.accept ?? "";
    },
    type: (element: HTMLInputElement, node: InputNode) => {
      element.type = node.type ?? "";
    },
    name: (element: HTMLInputElement, node: InputNode) => {
      element.name = node.name ?? "";
    },
    multiple: (element: HTMLInputElement, node: InputNode) => {
      element.multiple = node.multiple ?? false;
    },
  };
  private static readonly LAYOUT_MAP = {
    align: (element: HTMLElement, node: LayoutNode) => {
      if (node.align === undefined) {
        element.style.justifyContent = "";
        element.style.alignItems = "";
      } else {
        const align = node.column ? this.inverted(node.align!) : node.align!;
        switch (align) {
          case Align.TopLeft:
          case Align.TopCenter:
          case Align.TopRight:
          case Align.Top:
            element.style.alignItems = "flex-start";
            break;
          case Align.MiddleLeft:
          case Align.MiddleCenter:
          case Align.MiddleRight:
          case Align.Middle:
            element.style.alignItems = "center";
            break;
          case Align.BottomLeft:
          case Align.BottomCenter:
          case Align.BottomRight:
          case Align.Bottom:
            element.style.alignItems = "flex-end";
            break;
          case Align.Left:
          case Align.Center:
          case Align.Right:
            element.style.alignItems = "";
            break;
          default:
        }
        switch (align) {
          case Align.TopLeft:
          case Align.MiddleLeft:
          case Align.BottomLeft:
          case Align.Left:
            element.style.justifyContent = "flex-start";
            break;
          case Align.TopCenter:
          case Align.MiddleCenter:
          case Align.BottomCenter:
          case Align.Center:
            element.style.justifyContent = "center";
            break;
          case Align.TopRight:
          case Align.MiddleRight:
          case Align.BottomRight:
          case Align.Right:
            element.style.justifyContent = "flex-end";
            break;
          case Align.Top:
          case Align.Middle:
          case Align.Bottom:
            element.style.justifyContent = "";
            break;
          default:
        }
      }
    },
    animation: (element: HTMLElement, node: LayoutNode) => {
      element.style.animation = node.animation ?? "";
    },
    background: (element: HTMLElement, node: LayoutNode) => {
      element.style.background = node.background ?? "";
    },
    backgroundBlendMode: (element: HTMLElement, node: LayoutNode) => {
      element.style.backgroundBlendMode = node.backgroundBlendMode ?? "";
    },
    bold: (element: HTMLElement, node: LayoutNode) => {
      element.style.fontWeight = node.bold ? "bold" : "";
    },
    border: (element: HTMLElement, node: LayoutNode) => {
      if (node.border === undefined) {
        element.style.border = "";
        element.style.borderRadius = "";
        element.style.borderColor = "";
        element.style.borderWidth = "";
        element.style.borderStyle = "";
      } else {
        if (node.border.radius === undefined) {
          element.style.borderRadius = "";
        } else if (node.border.radius instanceof Array) {
          element.style.borderRadius = node.border.radius
            .map((t) => implicitPx(t))
            .join(" ");
        } else {
          element.style.borderRadius = implicitPx(node.border.radius);
        }
        if (node.border.color === undefined) {
          element.style.borderColor = "";
        } else if (node.border.color instanceof Array) {
          element.style.borderColor = node.border.color.join(" ");
        } else {
          element.style.borderColor = node.border.color;
        }
        if (node.border.width === undefined) {
          element.style.borderWidth = "";
        } else if (node.border.width instanceof Array) {
          element.style.borderWidth = node.border.width
            .map((t) => implicitPx(t))
            .join(" ");
        } else {
          element.style.borderWidth = implicitPx(node.border.width);
        }
        element.style.borderStyle =
          node.border.color !== undefined ? "solid" : "";
      }
    },
    boxSizing: (element: HTMLElement, node: LayoutNode) => {
      element.style.boxSizing =
        node.boxSizing !== undefined ? node.boxSizing : "";
    },
    className: (element: HTMLElement, node: LayoutNode) => {
      element.className = node.className !== undefined ? node.className : "";
    },
    color: (element: HTMLElement, node: LayoutNode) => {
      element.style.color = node.color ?? "";
    },
    cursor: (element: HTMLElement, node: LayoutNode) => {
      element.style.cursor = node.cursor !== undefined ? node.cursor : "";
    },
    display: (element: HTMLElement, node: LayoutNode) => {
      element.style.display = node.display !== undefined ? node.display : "";
    },
    filter: (element: HTMLElement, node: LayoutNode) => {
      element.style.filter = node.filter ?? "";
    },
    fontFamily: (element: HTMLElement, node: LayoutNode) => {
      element.style.fontFamily = node.fontFamily ?? "";
    },
    gap: (element: HTMLElement, node: LayoutNode) => {
      element.style.gap = node.gap !== undefined ? implicitPx(node.gap) : "";
    },
    grayScale: (element: HTMLElement, node: LayoutNode): void => {
      element.style.filter = node.grayScale === true ? "grayscale(100%)" : "";
    },
    grow: (element: HTMLElement, node: LayoutNode) => {
      element.style.flexGrow = node.grow !== undefined ? String(node.grow) : "";
    },
    height: (element: HTMLElement, node: LayoutNode) => {
      element.style.height =
        node.height !== undefined ? implicitPx(node.height) : "";
      this.updateImageSize(element, node);
    },
    image: (element: HTMLElement, node: LayoutNode) => {
      if (node.image === undefined) {
        element.style.backgroundImage = "";
      } else if (node.image instanceof Array) {
        element.style.backgroundImage = node.image
          .map((i) => `url(${i})`)
          .join(", ");
      } else {
        element.style.backgroundImage = `url(${node.image})`;
      }
      this.updateImageSize(element, node);
    },
    imagePosition: (element: HTMLElement, node: LayoutNode) => {
      if (node.imagePosition === undefined) {
        element.style.backgroundPosition = "";
      } else if (node.imagePosition instanceof Object) {
        element.style.backgroundPosition = `${implicitPx(
          node.imagePosition.left
        )} ${implicitPx(node.imagePosition.top)}`;
      } else {
        element.style.backgroundPosition = node.imagePosition;
      }
    },
    imageRepeat: (element: HTMLElement, node: LayoutNode) => {
      element.style.backgroundRepeat = node.imageRepeat ?? "";
    },
    imageScaling: (element: HTMLElement, node: LayoutNode) => {
      element.style.imageRendering = node.imageScaling ?? "";
    },
    imageSize: (element: HTMLElement, node: LayoutNode) => {
      this.updateImageSize(element, node);
    },
    letterSpacing: (element: HTMLElement, node: LayoutNode) => {
      element.style.letterSpacing =
        node.letterSpacing !== undefined ? implicitPx(node.letterSpacing) : "";
    },
    lineHeight: (element: HTMLElement, node: LayoutNode) => {
      element.style.lineHeight =
        node.lineHeight !== undefined ? implicitPx(node.lineHeight) : "";
    },
    margin: (element: HTMLElement, node: LayoutNode) => {
      if (node.margin === undefined) {
        element.style.margin = "";
      } else if (node.margin instanceof Object) {
        element.style.marginLeft = implicitPx(node.margin.left ?? "");
        element.style.marginRight = implicitPx(node.margin.right ?? "");
        element.style.marginTop = implicitPx(node.margin.top ?? "");
        element.style.marginBottom = implicitPx(node.margin.bottom ?? "");
      } else {
        element.style.margin = implicitPx(node.margin);
      }
    },
    mayClick: (element: HTMLElement, node: LayoutNode) => {
      QuickDOM.LAYOUT_MAP.onClick(element, node);
    },
    mayRightClick: (element: HTMLElement, node: LayoutNode) => {
      QuickDOM.LAYOUT_MAP.onRightClick(element, node);
    },
    maxWidth: (element: HTMLElement, node: LayoutNode) => {
      element.style.maxWidth =
        node.maxWidth !== undefined ? implicitPx(node.maxWidth) : "";
    },
    maxHeight: (element: HTMLElement, node: LayoutNode) => {
      element.style.maxHeight =
        node.maxHeight !== undefined ? implicitPx(node.maxHeight) : "";
    },
    minWidth: (element: HTMLElement, node: LayoutNode) => {
      element.style.minWidth =
        node.minWidth !== undefined ? implicitPx(node.minWidth) : "";
    },
    minHeight: (element: HTMLElement, node: LayoutNode) => {
      element.style.minHeight =
        node.minHeight !== undefined ? implicitPx(node.minHeight) : "";
    },
    nowrap: (element: HTMLElement, node: LayoutNode) => {
      element.style.whiteSpace = node.nowrap ? "nowrap" : "";
    },
    onClick: (element: HTMLElement, node: LayoutNode) => {
      element.onclick = this.addListener(node.onClick, node.mayClick);
    },
    onCreate: () => {
      // Only called on first render
    },
    onRightClick: (element: HTMLElement, node: LayoutNode) => {
      if (!isTouchDevice) {
        element.oncontextmenu = this.addListener(
          node.onRightClick,
          node.mayRightClick
        );
      }
    },
    onMouseOver: (element: HTMLElement, node: LayoutNode) => {
      if (!isTouchDevice) {
        element.onmouseover = this.addListener(node.onMouseOver);
      }
    },
    onMouseOut: (element: HTMLElement, node: LayoutNode) => {
      if (!isTouchDevice) {
        element.onmouseout = this.addListener(node.onMouseOut);
      }
    },
    onMouseEnter: (element: HTMLElement, node: LayoutNode) => {
      if (!isTouchDevice) {
        element.onmouseenter = this.addListener(node.onMouseEnter);
      }
    },
    onMouseLeave: (element: HTMLElement, node: LayoutNode) => {
      if (!isTouchDevice) {
        element.onmouseleave = this.addListener(node.onMouseLeave);
      }
    },
    onMouseDown: (element: HTMLElement, node: LayoutNode) => {
      if (!isTouchDevice) {
        element.onmousedown = this.addListener(node.onMouseDown);
      }
    },
    onMouseUp: (element: HTMLElement, node: LayoutNode) => {
      if (!isTouchDevice) {
        element.onmouseup = this.addListener(node.onMouseUp);
      }
    },
    onMouseMove: (element: HTMLElement, node: LayoutNode) => {
      if (!isTouchDevice) {
        element.onmousemove = this.addListener(node.onMouseMove);
      }
    },
    onWheel: (element: TrackedHTMLElement, node: LayoutNode) => {
      if (!isTouchDevice) {
        // All of this is to silent pesky console warning...
        if (element.oldEventListener) {
          element.removeEventListener("wheel", element.oldEventListener);
          element.oldEventListener = undefined;
        }
        if (node.onWheel !== undefined) {
          element.oldEventListener = this.addListener(node.onWheel)!;
          element.addEventListener("wheel", element.oldEventListener, {
            passive: false,
          });
        }
      }
    },
    opacity: (element: HTMLElement, node: LayoutNode) => {
      element.style.opacity =
        node.opacity !== undefined ? String(node.opacity) : "";
    },
    outline: (element: HTMLElement, node: LayoutNode) => {
      if (node.outline === undefined) {
        element.style.textShadow = "";
      } else if (node.outline instanceof Object) {
        const width = implicitPx(node.outline.width);
        element.style.textShadow = `-${width} -${width} 0 ${node.outline.color}, ${width} -${width} 0 ${node.outline.color}, -${width} ${width} 0 ${node.outline.color},${width} ${width} 0 ${node.outline.color}`;
      } else {
        element.style.textShadow = `-0.5px -0.5px 0 ${node.outline}, 0.5px -0.5px 0 ${node.outline}, -0.5px 0.5px 0 ${node.outline}, 0.5px 0.5px 0 ${node.outline}`;
      }
    },
    overflow: (element: HTMLElement, node: LayoutNode) => {
      element.style.overflow = node.overflow ?? "";
    },
    mixBlendMode: (element: HTMLElement, node: LayoutNode) => {
      element.style.mixBlendMode = node.mixBlendMode ?? "";
    },
    padding: (element: HTMLElement, node: LayoutNode) => {
      if (node.padding === undefined) {
        element.style.padding = "";
      } else if (node.padding instanceof Object) {
        element.style.paddingLeft = implicitPx(node.padding.left ?? "");
        element.style.paddingRight = implicitPx(node.padding.right ?? "");
        element.style.paddingTop = implicitPx(node.padding.top ?? "");
        element.style.paddingBottom = implicitPx(node.padding.bottom ?? "");
      } else {
        element.style.padding = implicitPx(node.padding);
      }
    },
    pointerEvents: (element: HTMLElement, node: LayoutNode) => {
      if (node.pointerEvents === undefined) {
        element.style.pointerEvents = "";
      } else {
        element.style.pointerEvents = node.pointerEvents ? "auto" : "none";
      }
    },
    polygon: (element: HTMLElement, node: LayoutNode) => {
      if (node.polygon === undefined) {
        element.style.clipPath = "";
      } else {
        element.style.clipPath = `polygon(${node.polygon
          .map(([a, b]) => `${implicitPx(a)} ${implicitPx(b)}`)
          .join(", ")})`;
      }
    },
    position: (element: HTMLElement, node: LayoutNode) => {
      if (node.position === undefined) {
        element.style.position = "";
      } else if (typeof node.position === "string") {
        element.style.position = node.position;
      } else {
        element.style.position = node.position.type ?? "";
      }
      if (node.position === undefined || typeof node.position === "string") {
        element.style.top = "";
        element.style.bottom = "";
        element.style.left = "";
        element.style.right = "";
      } else {
        element.style.top =
          node.position.top !== undefined ? implicitPx(node.position.top) : "";
        element.style.bottom =
          node.position.bottom !== undefined
            ? implicitPx(node.position.bottom)
            : "";
        element.style.left =
          node.position.left !== undefined
            ? implicitPx(node.position.left)
            : "";
        element.style.right =
          node.position.right !== undefined
            ? implicitPx(node.position.right)
            : "";
      }
    },
    shadow: (element: HTMLElement, node: LayoutNode): void => {
      if (node.shadow === undefined) {
        element.style.filter = "";
      } else {
        const shadow = typeof node.shadow === "boolean" ? {} : node.shadow;
        const offset =
          shadow.offset !== undefined ? implicitPx(shadow.offset) : "3px";
        const blurRadius =
          shadow.blur !== undefined ? implicitPx(shadow.blur) : "5px";
        const color = shadow.color ?? "#282828";
        element.style.filter = `drop-shadow(${offset} ${offset} ${blurRadius} ${color})`;
      }
    },
    showBackface: (element: HTMLElement, node: LayoutNode) => {
      if (node.showBackface === undefined) {
        element.style.backfaceVisibility = "";
      } else {
        element.style.backfaceVisibility = node.showBackface
          ? "visible"
          : "hidden";
      }
    },
    size: (element: HTMLElement, node: LayoutNode) => {
      element.style.fontSize = implicitPx(node.size!);
    },
    stretch: (element: HTMLElement, node: LayoutNode) => {
      element.style.alignSelf = node.stretch === true ? "stretch" : "";
    },
    textAlign: (element: HTMLElement, node: LayoutNode) => {
      element.style.textAlign = node.textAlign ?? "";
    },
    textTransform: (element: HTMLElement, node: LayoutNode) => {
      element.style.textTransform = node.textTransform ?? "";
    },
    transition: (element: HTMLElement, node: LayoutNode): void => {
      if (node.transition === undefined) {
        element.style.transition = "";
      } else {
        const transition = ((t) => {
          if (typeof t === "boolean") {
            return t ? [{ parameter: Parameter.All, time: 0.2 }] : [];
          } else {
            return t;
          }
        })(node.transition);
        element.style.transition = transition
          .map(({ parameter, time }) => {
            const arr =
              parameter instanceof Array
                ? parameter.map((p) => ({ parameter: p, time }))
                : [{ parameter, time }];
            return arr
              .map((p) => `${p.parameter} ${p.time ?? 0.2}s ease-in-out 0s`)
              .join(", ");
          })
          .join(", ");
      }
    },
    transform: (element: HTMLElement, node: LayoutNode): void => {
      if (node.transform === undefined) {
        element.style.transform = "";
      } else {
        element.style.transform = node.transform
          .map((t: any) => {
            if (t.perspective !== undefined) {
              return `perspective(${implicitPx(t.perspective)})`;
            } else if (t.rotateDeg !== undefined) {
              return `rotate(${t.rotateDeg}deg)`;
            } else if (t.rotateRad !== undefined) {
              return `rotate(${t.rotateRad}rad)`;
            } else if (t.rotateXdeg !== undefined) {
              return `rotateX(${t.rotateXdeg}deg)`;
            } else if (t.rotateYdeg !== undefined) {
              return `rotateY(${t.rotateYdeg}deg)`;
            } else if (t.rotateZdeg !== undefined) {
              return `rotateY(${t.rotateZdeg}deg)`;
            } else if (t.translateX !== undefined) {
              return `translateX(${implicitPx(t.translateX)})`;
            } else if (t.translateY !== undefined) {
              return `translateY(${implicitPx(t.translateY)})`;
            } else if (t.translateZ !== undefined) {
              return `translateZ(${implicitPx(t.translateZ)})`;
            } else if (t.scale !== undefined) {
              return `scale(${t.scale})`;
            } else if (t.scaleX !== undefined) {
              return `scaleX(${t.scaleX})`;
            } else if (t.scaleY !== undefined) {
              return `scaleY(${t.scaleY})`;
            } else if (t.scaleZ !== undefined) {
              return `scaleZ(${t.scaleZ})`;
            } else {
              return "";
            }
          })
          .join(" ");
      }
    },
    transformOrigin: (element: HTMLElement, node: LayoutNode): void => {
      if (node.transformOrigin instanceof Object) {
        element.style.transformOrigin = `${implicitPx(
          node.transformOrigin.x
        )} ${implicitPx(node.transformOrigin.y)}`;
      } else {
        element.style.transformOrigin = node.transformOrigin ?? "";
      }
    },
    video: (element: HTMLElement, node: LayoutNode) => {
      const video = document.createElement("video") as HTMLVideoElement;
      this.updateVideoContent(video, node);
      this.removeChildren(element);
      element.appendChild(video);
      node.onCreate?.(element);
    },
    width: (element: HTMLElement, node: LayoutNode) => {
      element.style.width =
        node.width !== undefined ? implicitPx(node.width) : "";
      this.updateImageSize(element, node);
    },
    wrap: (element: HTMLElement, node: LayoutNode) => {
      element.style.flexWrap = node.wrap === true ? "wrap" : "";
    },
    whiteSpace: (element: HTMLElement, node: LayoutNode) => {
      element.style.whiteSpace = node.whiteSpace ?? "";
    },
    zIndex: (element: HTMLElement, node: LayoutNodeProps) => {
      element.style.zIndex =
        node.zIndex !== undefined ? String(node.zIndex) : "";
    },
    input: (element: HTMLElement, node: LayoutNode) => {
      const input = document.createElement("input") as HTMLInputElement;
      this.updateInputContent(input, node);
      this.removeChildren(element);
      element.appendChild(input);
      if (node.input?.autoFocus || node.input?.autoSelect) {
        setTimeout(() => {
          if (node.input.autoSelect) {
            input.setSelectionRange(0, input.value.length);
          }
          if (node.input.autoFocus) {
            input.focus();
          }
        }, 0);
      }
      node.onCreate?.(element);
    },
    text: (element: HTMLElement, node: LayoutNode) => {
      const span = document.createElement("span") as HTMLSpanElement;
      this.updateTextContent(span, node);
      this.removeChildren(element);
      element.appendChild(span);
      node.onCreate?.(element);
    },
    row: (element: HTMLElement, node: LayoutNode) => {
      element.style.flexDirection = "row";
      this.removeChildren(element);
      node.row!.forEach((childNode) => {
        if (childNode !== false) {
          element.appendChild(this.renderNode(childNode));
        }
      });
    },
    column: (element: HTMLElement, node: LayoutNode) => {
      element.style.flexDirection = "column";
      this.removeChildren(element);
      node.column!.forEach((childNode) => {
        if (childNode !== false) {
          element.appendChild(this.renderNode(childNode));
        }
      });
    },
    ref: (element: HTMLElement, node: LayoutNode) => {
      node.ref?.(element);
    },
  };
  private static readonly TEXT_MAP = {
    text: (element: HTMLSpanElement, node: TextNode) => {
      this.removeChildren(element);
      element.textContent = node.text;
    },
    ref: (element: HTMLSpanElement, node: TextNode) => {
      node.ref?.(element);
    },
  };
  private static readonly VIDEO_MAP = {
    autoplay: (element: HTMLVideoElement, node: VideoNode) => {
      element.autoplay = node.autoplay ?? false;
    },
    loop: (element: HTMLVideoElement, node: VideoNode) => {
      element.loop = node.loop ?? false;
    },
    muted: (element: HTMLVideoElement, node: VideoNode) => {
      element.muted = node.muted ?? false;
    },
    playsInline: (element: HTMLVideoElement, node: VideoNode) => {
      element.playsInline = node.playsInline ?? false;
    },
    source: (element: HTMLVideoElement, node: VideoNode) => {
      element.src = node.source ?? "";
    },
  };
  public static render(container: HTMLElement, root: Component): void {
    action(() => {
      const rootElement = this.renderNode(root.layout);
      container.appendChild(rootElement);
      reaction(
        () => root.layout,
        (newLayout, oldLayout) => {
          this.updateRecursively(rootElement, newLayout, oldLayout);
        }
      );
    })();
  }
  private static addListener<T>(
    fn: ((event: T) => boolean | void) | undefined,
    mayUse?: boolean
  ): ((event: Event) => void) | null {
    if (fn === undefined) {
      return null;
    } else {
      return action((event: Event) => {
        if (mayUse !== false) {
          if ((fn as (event: Event) => boolean)?.(event)) {
            event.stopPropagation();
            event.preventDefault();
          }
        } else {
          event.preventDefault();
        }
      });
    }
  }
  private static inverted(align: Align): Align {
    switch (align) {
      case Align.TopCenter:
        return Align.MiddleLeft;
      case Align.TopRight:
        return Align.BottomLeft;
      case Align.MiddleLeft:
        return Align.TopCenter;
      case Align.MiddleRight:
        return Align.BottomCenter;
      case Align.BottomLeft:
        return Align.TopRight;
      case Align.BottomCenter:
        return Align.MiddleRight;
      case Align.Top:
        return Align.Left;
      case Align.Left:
        return Align.Top;
      case Align.Center:
        return Align.Middle;
      case Align.Middle:
        return Align.Center;
      case Align.Right:
        return Align.Bottom;
      case Align.Bottom:
        return Align.Right;
      default:
        return align;
    }
  }
  private static removeChildren(element: HTMLElement): void {
    while (element.firstChild) {
      element.removeChild(element.firstChild);
    }
    element.textContent = "";
  }
  private static renderNode(node: LayoutNode): HTMLElement {
    const element = document.createElement("div") as HTMLElement;
    Object.keys(node).forEach((key) => {
      if (key !== "key") {
        const k = key as keyof typeof QuickDOM.LAYOUT_MAP;
        if (QuickDOM.LAYOUT_MAP[k]) {
          QuickDOM.LAYOUT_MAP[k](element, node);
        } else {
          console.warn(`${k} is not a handled!`);
        }
      }
    });
    node.onCreate?.(element);
    return element;
  }
  private static updateImageSize(element: HTMLElement, node: LayoutNode): void {
    if (node.imageSize === undefined) {
      element.style.backgroundSize =
        node.image !== undefined &&
        node.width !== undefined &&
        node.height !== undefined
          ? `${implicitPx(node.width)} ${implicitPx(node.height)}`
          : "";
    } else if (node.imageSize instanceof Object) {
      element.style.backgroundSize = `${implicitPx(
        node.imageSize.width
      )} ${implicitPx(node.imageSize.height)}`;
    } else {
      element.style.backgroundSize = node.imageSize;
    }
  }
  private static updateInputContent(
    input: HTMLInputElement,
    node: LayoutNode
  ): void {
    if (node.input === undefined) {
      this.removeChildren(input);
    } else {
      Object.keys(node.input as InputNode).forEach((key) => {
        const ki = key as keyof typeof this.INPUT_MAP;
        const kl = key as keyof typeof this.LAYOUT_MAP;
        if (QuickDOM.INPUT_MAP[ki]) {
          QuickDOM.INPUT_MAP[ki](input, node.input as InputNode);
        } else if (QuickDOM.LAYOUT_MAP[kl]) {
          QuickDOM.LAYOUT_MAP[kl](input, node.input as LayoutNode);
        } else {
          throw new Error(`Unknown input property: ${key}`);
        }
      });
    }
  }
  private static updateRecursively(
    element: HTMLElement,
    newLayout: LayoutNode,
    oldLayout: LayoutNode
  ): void {
    Object.keys(newLayout).forEach((key) => {
      if (key === "key") {
        return;
      }
      const k = key as keyof typeof QuickDOM.LAYOUT_MAP;
      if (
        k === "column" ||
        k === "row" ||
        !objectsAreEqual(newLayout[k], oldLayout[k])
      ) {
        if (k === "column" || k === "row") {
          const newChildren = newLayout[k]?.filter(
            (node) => node !== false
          ) as LayoutNode[];
          const oldChildren = oldLayout[k]?.filter(
            (node) => node !== false
          ) as LayoutNode[];
          if (oldChildren === undefined) {
            this.removeChildren(element);
            element.style.flexDirection = k;
          }
          let newIndex = 0;
          let oldIndex = 0;
          let domIndex = 0;
          while (
            newIndex < newChildren.length &&
            oldChildren !== undefined &&
            oldIndex < oldChildren.length
          ) {
            if (objectsAreEqual(newChildren[newIndex], oldChildren[oldIndex])) {
              // Element is still here. Preserve.
              newIndex += 1;
              oldIndex += 1;
              domIndex += 1;
            } else if (
              oldIndex + 1 < oldChildren.length &&
              objectsAreEqual(newChildren[newIndex], oldChildren[oldIndex + 1])
            ) {
              // Element is gone. Remove it.
              element.removeChild(element.childNodes[domIndex]);
              oldIndex += 1;
            } else if (
              newIndex + 1 < newChildren.length &&
              objectsAreEqual(newChildren[newIndex + 1], oldChildren[oldIndex])
            ) {
              // This is a new element. Insert it.
              element.insertBefore(
                this.renderNode(newChildren[newIndex]),
                element.childNodes[domIndex]
              );
              domIndex += 1;
              newIndex += 1;
            } else if (
              newChildren![newIndex].key !== oldChildren![oldIndex].key
            ) {
              // Key is not same. Replace element
              element.replaceChild(
                this.renderNode(newChildren[newIndex]),
                element.childNodes[domIndex]
              );
              domIndex += 1;
              newIndex += 1;
              oldIndex += 1;
            } else {
              // Key is the same or non-existing. Update element
              this.updateRecursively(
                element.childNodes[domIndex] as HTMLElement,
                newChildren![newIndex],
                oldChildren![oldIndex]
              );
              domIndex += 1;
              newIndex += 1;
              oldIndex += 1;
            }
          }
          while (newIndex < newChildren.length) {
            element.appendChild(this.renderNode(newChildren[newIndex]));
            newIndex += 1;
          }
          while (oldChildren !== undefined && oldIndex < oldChildren.length) {
            element.removeChild(element.lastChild!);
            oldIndex += 1;
          }
        } else if (k === "text") {
          if (oldLayout.text === undefined) {
            QuickDOM.LAYOUT_MAP.text(element, newLayout);
          } else {
            this.updateTextContent(
              element.firstChild as HTMLSpanElement,
              newLayout
            );
            // Reset old text styles
            if (typeof oldLayout.text !== "string") {
              Object.keys(oldLayout.text).forEach((oldKey) => {
                const kt = oldKey as keyof typeof this.TEXT_MAP;
                const kl = oldKey as keyof typeof this.LAYOUT_MAP;
                if (typeof newLayout.text === "string") {
                  if (this.TEXT_MAP[kt]) {
                    this.TEXT_MAP[kt](
                      element.firstChild as HTMLSpanElement,
                      {} as TextNode
                    );
                  } else if (this.LAYOUT_MAP[kl]) {
                    this.LAYOUT_MAP[kl](
                      element.firstChild as HTMLDivElement,
                      {} as LayoutNode
                    );
                  } else {
                    throw new Error(`Unknown text property: ${oldKey}`);
                  }
                } else if (!(oldKey in newLayout.text!)) {
                  if (this.TEXT_MAP[kt]) {
                    this.TEXT_MAP[kt](
                      element.firstChild as HTMLSpanElement,
                      newLayout.text as TextNode
                    );
                  } else if (this.LAYOUT_MAP[kl]) {
                    this.LAYOUT_MAP[kl](
                      element.firstChild as HTMLDivElement,
                      newLayout.text as LayoutNode
                    );
                  } else {
                    throw new Error(`Unknown text property: ${oldKey}`);
                  }
                }
              });
            }
          }
        } else if (k === "input") {
          if (oldLayout.input === undefined) {
            QuickDOM.LAYOUT_MAP.input(element, newLayout);
          } else {
            this.updateInputContent(
              element.firstChild as HTMLInputElement,
              newLayout
            );
            // Reset old input styles
            Object.keys(oldLayout.input).forEach((oldKey) => {
              if (!(oldKey in newLayout.input!)) {
                QuickDOM.INPUT_MAP[oldKey as keyof typeof QuickDOM.INPUT_MAP](
                  element.firstChild as HTMLInputElement,
                  newLayout.input as InputNode
                );
              }
            });
          }
        } else if (k === "video") {
          if (oldLayout.video === undefined) {
            QuickDOM.LAYOUT_MAP.video(element, newLayout);
          } else {
            this.updateVideoContent(
              element.firstChild as HTMLVideoElement,
              newLayout
            );
            // Reset old input styles
            Object.keys(oldLayout.video).forEach((oldKey) => {
              if (!(oldKey in newLayout.video!)) {
                QuickDOM.VIDEO_MAP[oldKey as keyof typeof QuickDOM.VIDEO_MAP](
                  element.firstChild as HTMLVideoElement,
                  newLayout.video as VideoNode
                );
              }
            });
          }
        } else if (QuickDOM.LAYOUT_MAP[k]) {
          QuickDOM.LAYOUT_MAP[k](element, newLayout);
        } else {
          console.warn(`${k} is not a handled!`);
        }
      }
    });
    Object.keys(oldLayout).forEach((key) => {
      if (!(key in newLayout)) {
        if (
          key !== "column" &&
          key !== "row" &&
          key !== "text" &&
          key !== "input" &&
          key !== "video"
        ) {
          const k = key as keyof typeof QuickDOM.LAYOUT_MAP;
          if (QuickDOM.LAYOUT_MAP[k]) {
            QuickDOM.LAYOUT_MAP[k](element, newLayout);
          } else {
            throw new Error(
              `Unknown layout property: ${k} in layout ${JSON.stringify(
                newLayout
              )}`
            );
          }
        }
      }
    });
    if (
      newLayout.column === undefined &&
      newLayout.row === undefined &&
      newLayout.text === undefined &&
      newLayout.input === undefined &&
      newLayout.video === undefined
    ) {
      this.removeChildren(element);
    }
  }
  private static updateTextContent(
    span: HTMLSpanElement,
    node: LayoutNode
  ): void {
    const { text } = node;
    if (text === undefined) {
      this.removeChildren(span);
    } else if (typeof text === "string") {
      this.removeChildren(span);
      span.textContent = text;
    } else {
      Object.keys(text as TextNode).forEach((key) => {
        const kt = key as keyof typeof this.TEXT_MAP;
        const kl = key as keyof typeof this.LAYOUT_MAP;
        if (this.TEXT_MAP[kt]) {
          this.TEXT_MAP[kt](span, text as TextNode);
        } else if (this.LAYOUT_MAP[kl]) {
          this.LAYOUT_MAP[kl](span, text as LayoutNode);
        } else {
          throw new Error(`Unknown text property: ${key}`);
        }
      });
    }
  }
  private static updateVideoContent(
    video: HTMLVideoElement,
    node: LayoutNode
  ): void {
    if (node.video === undefined) {
      this.removeChildren(video);
    } else {
      Object.keys(node.video as VideoNode).forEach((key) => {
        const ki = key as keyof typeof this.VIDEO_MAP;
        const kl = key as keyof typeof this.LAYOUT_MAP;
        if (QuickDOM.VIDEO_MAP[ki]) {
          QuickDOM.VIDEO_MAP[ki](video, node.video as VideoNode);
        } else if (QuickDOM.LAYOUT_MAP[kl]) {
          QuickDOM.LAYOUT_MAP[kl](video, node.video as LayoutNode);
        } else {
          throw new Error(`Unknown input property: ${key}`);
        }
      });
    }
  }
}
